import React, { Component } from 'react';
// import Placeholder from '../components/Placeholder';
// import MutationWrapper from '../components/MutationWrapper';
import { Redirect } from 'react-router-dom';
import PropTypes from 'prop-types';
import Grid from '@material-ui/core/Grid';
import { withStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import { Formik, Form } from 'formik';

import Input from '@material-ui/core/Input';
import InputLabel from '@material-ui/core/InputLabel';
import FormHelperText from '@material-ui/core/FormHelperText';
import FormControl from '@material-ui/core/FormControl';
import CircularProgress from '@material-ui/core/CircularProgress';
// import * as yup from 'yup';

import { Mutation, Query } from "react-apollo";
import { LOGIN } from '../graphql/mutation/remote';
import { LOCALLOGIN } from '../graphql/mutation/local';
import { LOCALLOGIN as LOCALLOGIN_QUERY } from '../graphql/query/local';

const styles = {
    root: {
        flexGrow: 1,
        marginTop: "5rem"
    },
    card: {
        minWidth: 275,
        margin: "0 2%"
    },
    header: {
        marginBottom: "1rem",
        fontSize: "2rem"
    },
    progress: {
        margin: "1rem",
    },
};

class Login extends Component {
    render() {
        const { classes } = this.props;
        return (
            <Query query={LOCALLOGIN_QUERY}>
                {
                    ({data, refetch})=> {
                        console.log("LOGIN QUERY data is ", data);
                        if(data.localLogin && data.localLogin.token)
                        {
                            return (
                                <Redirect to="/"></Redirect>
                            );
                        }
                        return (
                        <Mutation mutation={LOGIN}>
                            {(login, params) => (
                                <Mutation mutation={LOCALLOGIN}>
                                    {(locallogin, params) => (
                                        <Formik
                                            initialValues={{
                                                username: "",
                                                password: "",
                                            }}
                                            validate={values => {
                                                // same as above, but feel free to move this into a class method now.
                                                let errors = {};
                                                if (!values.username) {
                                                    errors.username = 'Required';
                                                }
                                                return errors;
                                            }}
                                            onSubmit={(values, { props, resetForm, setErrors, setSubmitting }) => {
                                                console.log(values);
                                                console.log(props);
                                                login({variables: values}).then(({data, error})=>{
                                                    setSubmitting(false);
                                                    console.log(data);
                                                    console.log(error);
                                                    const { login: {token} } = data;
                                                    window.localStorage.setItem('userToken', token);
                                                    locallogin({variables: {token}}).then(({data, error}) => {
                                                        console.log(data);
                                                        window.location.href = "/";
                                                    });

                                                }, error => {
                                                    setSubmitting(false);
                                                });
                                            }}
                                            render={({
                                                values,
                                                errors,
                                                touched,
                                                handleChange,
                                                handleBlur,
                                                handleSubmit,
                                                isSubmitting,
                                            }) => {
                                                return(
                                                <Form>
                                                    <Grid container className={classes.root} justify="center" alignItems="center" align="center">
                                                        <Grid item xs={12} sm={6} md={5}>
                                                            <Card className={classes.card}>
                                                                <CardContent className="ph4">
                                                                    <Typography variant="display1" className="mb2">Login</Typography>
                                                                    <FormControl className="w-100">
                                                                        <InputLabel htmlFor="username">Username</InputLabel>
                                                                        <Input name="username" onChange={handleChange}></Input>
                                                                        {
                                                                            errors.username ? <FormHelperText error={true}>Invalid Username!</FormHelperText>: null
                                                                        }
                                                                    </FormControl>
                                                                    <FormControl className="w-100 mt3">
                                                                        <InputLabel htmlFor="password">Password</InputLabel>
                                                                        <Input name="password" type="password" onChange={handleChange}></Input>
                                                                    </FormControl>
                                                                </CardContent>
                                                                <CardActions>
                                                                    <Grid container justify="flex-end">
                                                                        {
                                                                            isSubmitting ? <CircularProgress className={classes.progress}/>:null
                                                                        }
                                                                        <Button size="large" type="submit" disabled={isSubmitting}>Login</Button>
                                                                    </Grid>
                                                                </CardActions>
                                                            </Card>
                                                        </Grid>
                                                    </Grid>
                                                </Form>
                                            )}}
                                        />
                                    )}
                                </Mutation>
                            )}
                        </Mutation>
                    )}
                }
            </Query>
        );
    }
}

Login.propTypes = {
    classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(Login);



